* {
  box-sizing: border-box;
}

ul {
  list-style: none;
}

img {
  display: block;
}

.search-form {
  text-align: center;
  background-color: #7878ed;
  margin-bottom: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.gallery {
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 30px;
  padding: 0;
  display: flex;
}

.photo-card {
  width: calc(25% - 22.5px);
}

.thumb {
  height: 200px;
}

.prev-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info {
  border-bottom: 1px solid #cbc5c5;
  border-left: 1px solid #cbc5c5;
  border-right: 1px solid #cbc5c5;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  justify-content: space-around;
  margin: 0;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

.info-item > b {
  margin-bottom: 5px;
}

.load-more {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.spinner {
  margin-right: 5px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.up-button {
  z-index: 2;
  width: 40px;
  height: 40px;
  color: #fff;
  background-color: rgba(120, 120, 237, .8);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 5px;
  text-decoration: none;
  display: flex;
  position: fixed;
  bottom: 80px;
  right: 80px;
  box-shadow: 0 0 1px 2px #fff;
}

